<template>
	<div class="content">
		<div class="login-box" id="rrapp">
			<div class="login-logo">
				<span></span>
				<h1>中均OA管理系统</h1>
			</div>
			<div class="from">
				<div class="user list">
					<el-input v-model="userName" placeholder="账号"></el-input>
				</div>
				<div class="password list">
					<el-input type="password" v-model="password" placeholder="密码"></el-input>
				</div>
				<div class="sub list" @click="login">
					登录
				</div>
			</div>

		</div>

	</div>
</template>
<script>
import { mapGetters, mapState, mapMutations, mapActions } from "vuex";
export default {
  data() {
    return {
      token: "",
      src: require("../../assets/login/icon.png"),
      userName: "",
      password: ""
    };
  },
  computed: {
    ...mapState({
      //user: state => state.login.user
    })
  },
  methods: {
    login() {
      const self = this;
      if (self.$fn.isNull(self.userName) || self.$fn.isNull(self.password)) {
        self.$message.error("请填写用户名或密码");
        return;
      }
      let params = {
        username: self.$fn.trim(self.userName),
        password: self.$fn.trim(self.password)
      };
      self.$post(self.$API.POST_SYS_LOGIN, params).then(result => {
        if (result.code == "0") {
          if (!self.$fn.isNull(result.token)) {
            self.token = result.token;
            sessionStorage.setItem("myron", self.token);
          }
          const user = result.username || "admin";
          sessionStorage.setItem("user", user);
          self.$router.push({
            path: "/index"
          });
        } else {
          this.$message({
            message: result.msg,
            type: "warning"
          });
        }
      });
    }
  },
  watch: {},
  created() {
    sessionStorage.setItem("myron", "");
  },
  mounted() {
    //回车键登录
    const self = this;
    document.onkeydown = function(event) {
      var e = event || window.event || arguments.callee.caller.arguments[0];
      if (e && e.keyCode == 13) {
        self.login();
      }
    };
  }
};
</script>
<style lang="scss" scoped type="text/css">
@import "./index.scss";
</style></style>


